.login-page {
	height: 100vh;
	display: flex;
	.sidebar {
		background: #fff;
		max-height: 100vh;
		display: flex;
		flex: 1 0 380px;
		flex-direction: column;
		justify-content: space-between;
		padding: 30px 0;
		position: relative;
		box-shadow: rgba(3, 27, 78, 0.1) 0px 2px 4px;
		h1 {
			margin-top: 0;
		}
		h3 {
			margin-bottom: 0;
		}
		> div {
			padding: 0 30px;
		}
		.header {
			border-bottom: 1px solid #efefef;
		}
		.body {
			height: 40vh;
		}
	}
	.image {
		width: 100%;
		background-color: beige;
		background-size: cover;
		background-position: center center;
	}
	form > div {
		margin-bottom: 24px;
	}
}
