.form-group {
	margin-bottom: 20px;
	.form-group {
		margin-bottom: 0;
	}
	&.multiple {
		display: flex;
		justify-content: space-between;
		> * {
			flex: 1;
		}
		> :nth-child(2),
		> :nth-child(3) {
			margin: 0 20px;
		}
		> :nth-child(3) {
			margin-left: 0;
		}
		> :last-child {
			margin: 0;
		}
		.ml2 {
			margin-left: 40px;
		}
	}
}

.form-grouped.bg {
	padding: 20px 0;
	margin: 20px 0;
}
