.site-header {
	position: fixed;
	padding: 14px 40px;
	display: flex;
	align-items: center;
	background: #ffffff;
	box-shadow: rgba(3, 27, 78, 0.1) 0px 2px 4px;
	width: 100%;
	justify-content: space-between;
	box-sizing: border-box;
	align-items: center;
	z-index: 99;
	top: 0;
	h3 {
		margin: 0;
		letter-spacing: 0.3px;
		color: #3f51b5;
	}
	position: relative;
	.logo {
		position: relative;
		:before {
			content: '';
			width: 1px;
			background-color: #ececec;
			height: 68px;
			position: absolute;
			top: -23px;
			right: -36px;
		}
	}
	.btn-group {
		display: flex;
		align-items: center;
		.user-role {
			margin: 0;
			margin-right: 16px;
			color: #b1b1b1;
			border-right: 1px solid;
			padding-right: 14px;
			font-size: 14px;
		}
		.addbutton {
			margin-right: 14px;
		}
	}
	nav {
		a {
			padding: 21px;
			text-decoration: none;
			color: #444444;
			font-weight: 500;
			text-transform: uppercase;
			font-size: 14px;
			transition: all 0.4s ease;
			&.active,
			&:hover,
			&:focus {
				box-shadow: 0 4px 0 #3f51b5;
				color: #3f51b5;
			}
		}
	}
}
